import React, { useState, useEffect } from 'react'
import ReactDOM from 'react'
import Header from './header/Header.jsx'
import SearchBar from './header/SearchBar.jsx'
import WeatherToday from './content/WeatherToday.jsx'
import WeatherWeek from './content/WeatherWeek.jsx'

export default function Weather() {
    let [location, setLocation] = useState('Lviv')
    let [weatherData, setWeatherData] = useState('')

    function handlerLocation(value) {
        setLocation(value)
    }
    useEffect(() => {
        let weatherURL = `https://api.openweathermap.org/data/2.5/forecast?q=${location}&appid=3ccdcc7d9418e33a6d5833089853f381`
        fetch(weatherURL)
            .then(res => res.json())
            .then(data => setWeatherData(data.list))
    }, [location])
    if (weatherData !== '') {
        let ff = (weatherData.slice(0, 8))
        console.log(ff)
        //weatherData.forEach(elem => console.log(elem.dt_txt.slice(1, -4)))

    }

    return (

        <div className='weather-app'>
            <Header value={location} handlerLocation={handlerLocation} />
            <WeatherToday weatherData={weatherData.slice(0, 8)} />
            <WeatherWeek />
        </div>
    )
}



